.menu_item {
  display: block;
  line-height: 51px;
  height: 51px;
  position: relative;
  text-decoration: none;
  margin: 20px 0;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out; }
  .menu_item .icon {
    margin: 0 15%; }
  .menu_item .title {
    color: #EE86B7;
    font-size: 9px;
    letter-spacing: 2px;
    position: absolute;
    bottom: 0;
    display: block;
    width: 100%;
    text-align: center;
    opacity: 0;
    -webkit-transition: all 0.5s ease-out;
    transition: all 0.5s ease-out; }

.menu_item:hover .icon {
  display: none; }

.menu_item:hover .title {
  opacity: 1; }
