.thinking_wrapper {
  background-color: #6E7376;
  width: 100vw;
  height: 100%;
  right: 0;
  position: absolute;
  z-index: 0;
  color: white;
  font-family: Nunito, sans-serif; }

.thinking {
  width: 350px;
  position: absolute;
  left: 0;
  right: 0;
  top: 30vh;
  bottom: 0;
  margin: auto;
  overflow: auto; }

.thinking_text {
  text-align: center; }

.progress_bar {
  position: relative;
  height: 20px;
  width: 340px;
  border-radius: 50px;
  border: 1px solid #99CED4; }

.filler {
  background: #EE86B7;
  height: 100%;
  border-radius: inherit;
  transition: width 1.4s ease-in; }
